#check-mark {

    transform:scale(0.5);
    max-height: 100px;
    display: block;
    position: fixed;
    right: 2%;
    bottom: 40%;
    border-radius: 600px;
    padding: 50px;
    background: #b5e69da8;
    border: 2px solid #128700;

}

#x-cross {

    transform:scale(0.5);
    max-height: 100px;
    position: fixed;
    left: 5%;
    bottom: 40%;
    border-radius: 600px;
    padding: 50px;
    background: #ff8f8f;
    border: 2px solid #ba0000;

}

.swiping-page--wrapper{
    display: grid;
    height: 100vh;
    place-content: center;
    background: linear-gradient(270deg, #ff655b, #ff4c6a, #f8708f, #80c260, #50cfbd, #c650cf);
    background-size: 1200% 1200%;

    -webkit-animation: AnimationName 36s ease infinite;
    -moz-animation: AnimationName 36s ease infinite;
    animation: AnimationName 36s ease infinite;
}

/* from gradient-animator.com */
@-webkit-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
    
    

img.avatar{
    width: 100%;
    border-radius: 50%;
}
.card{
    height: 500px;
    width: 300px;
    padding: 20px;
    box-shadow: 0px 0px 5px 1px black;
    border-radius: 4px;
}

#viewport {
    
    padding: 40px;
    width: 500px; 
    height: 500px;
    
    border: 10px;
    position: relative;
    place-content: center;
}




