#viewport li {

    
    overflow:auto;
    width: auto; 
    height: auto; 
    list-style: none; 
    
    border-radius: 20px; 
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    top: 0; 
    box-shadow: 0 0 2px rgba(0,0,0,.2), 1px 1px 1px rgba(0,0,0,.2); 
    line-height: 3px; 
    text-align: center; 
    font-size: 10px; 

    background: #8ac9e2;
   
    
    cursor: default;
    place-content: center;

    transition: background-color 550ms, color 250ms;
}

#viewport:hover{
    
    background: #8ac9e2;
}

#viewport li.in-deck:nth-child(3) {
    top: 2px; transform: translate(2px, 2px) rotate(0.4deg);
}

#viewport li.in-deck:nth-child(2) {
    top: 4px; transform: translate(-4px, -2px) rotate(-1deg);
}

